body {
  margin: 0;
  font-family: "Arial", sans-serif;
  background-color: #141414;
  color: white;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #141414;
  padding: 20px;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search-bar {
  margin: 20px;
}

.search-bar input {
  padding: 10px;
  font-size: 16px;
  width: 300px;
  border: none;
  border-radius: 5px;
}

.search-bar button {
  padding: 10px;
  font-size: 16px;
  margin-left: 10px;
  border: none;
  border-radius: 5px;
  background-color: #e50914;
  color: white;
  cursor: pointer;
}

.movie-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px;
}

.movie {
  margin: 10px;
  text-align: left;
  background-color: #444;
  padding: 10px;
  border-radius: 5px;
  width: 200px;
  cursor: pointer;
  transition: transform 0.3s;
}

.movie:hover {
  transform: scale(1.05);
}

.movie img {
  max-width: 100%;
  border-radius: 5px;
}

.movie-info {
  padding: 10px;
}

.movie-info h3 {
  margin: 10px 0;
}

.error {
  color: red;
  margin: 20px;
}

.movie-details {
  padding: 20px;
  text-align: left;
}
